import React, { useRef, useEffect } from 'react';

import anime from 'animejs';
import styled from '@emotion/styled';

const hide = (el: HTMLElement, speed: number) =>
  anime({
    targets: el,
    duration: speed,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    opacity: [1, 0],
    complete: () => (el.style.display = 'none')
  });
const show = (el: HTMLElement, speed: number) =>
  anime({
    begin: () => (el.style.display = 'initial'),
    targets: el,
    duration: speed,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    opacity: [0, 1]
  });

const Span = styled.span`
  display: none;
  opacity: 0;
`;

interface FadeProps {
  visible: boolean;
  speed?: number;
}
export const Fade: React.FC<FadeProps> = ({
  children,
  visible,
  speed = 100
}) => {
  const didMountRef = useRef(false);
  const ref = useRef<HTMLSpanElement>(null);

  useEffect(() => {
    const el = ref.current!;
    if (!didMountRef.current) {
      didMountRef.current = true;
      if (!visible) {
        return;
      }
    }
    const anim = visible ? show(el, speed) : hide(el, speed);
    return anim.pause;
  }, [visible, speed]);

  return <Span ref={ref}>{children}</Span>;
};
