import React from 'react';

import styled from '@emotion/styled';
import { mdiCheckBoxOutline, mdiImageOutline } from '@mdi/js';
import { ActionBar as UActionBar } from '../../components/ActionBar/ActionBar';
import { Icon } from '../../components/Icon/Icon';
import { Input } from '../../components/Input/Input';
import { useRouter } from 'react-router5';

const ActionBar = styled(UActionBar)`
  position: fixed;
  bottom: 0;
`;
const Actions = styled.div`
  display: flex;
`;
const Action = styled(Icon)`
  padding: 0.5em;
  width: 2.5em;
  height: 2.5em;
`;

interface NewActionBarProps {}
export const NewActionBar: React.FC<NewActionBarProps> = () => {
  const { navigate } = useRouter();

  return (
    <ActionBar>
      <Input
        onClick={() => navigate('new')}
        readOnly={true}
        type="text"
        placeholder="Take a note..."
      />
      <Actions>
        <Action onClick={() => navigate('new', { checked: true })}>
          {mdiCheckBoxOutline}
        </Action>
        <Action onClick={() => navigate('new', { image: true })}>
          {mdiImageOutline}
        </Action>
      </Actions>
    </ActionBar>
  );
};
